export default {
	data() {
		return {
			user_id: 0,
			img_object: [{}],
			/*img_object
			[{
				"id":
				"title":
				"img":
			},]
			*/
		}
	},

	onLoad: function(e) {
		
	},

	methods: {
		/*打开获取和主界面相同信息*/
		get_info(e) {
			uni.request({
				url: 'http://localhost/api/habit/present',
				method: 'GET',
				data:{
					uid:this.user_id
				},
				/*返回值*/
				success: (res) => {
					/*错误接收暂且不管*/
					// err = res.err,
					this.user_id = res.uid,
					this.img_object = res.data
				}
			})
		},

		/*点击删除*/
		click_img() {
			uni.request({
				url:'http://localhost/api/habit/delete',
				method:'GET',
				data:{
					uid:this.user_id,
					id:this.img_object.id
				}
			})
		},
	}
}
